body {
    overflow-x: hidden;
  }
  
  .sd {
    overflow-x: hidden !important;
  }
  
  .area {
    max-width: 900px;
    width: 100%;
    float: none !important;
    margin: auto;
    display: flex;
  }
  
  @media (max-width: 620px) {
    .area {
      max-width: 619px !important;
      width: 100% !important;
      margin: 0 auto;
      display: block;
    }
    form {
      padding-top: 1rem;
      flex-direction: column;
    }
    .deb {
      margin-bottom: 1.5rem;
      width: 23rem !important;
      padding: 5px;
      padding-bottom: 2px !important;
      max-height: 150px;
      z-index: 100 !important;
    }
    .deb2 {
      height: 60px !important;
      padding: 2px;
      margin-bottom: 1.5rem;
      position: absolute !important;
      top: 23vh;
      width: 23rem !important;
    }
    .btn {
      width: 91% !important;
      height: 40px;
      position: absolute !important;
      top: 33vh;
      background-color: #2557a7;
      border: 0.0625rem solid transparent !important;
    }
    .btn a {
      color: white;
      font-weight: 600;
    }
    .btn a:hover {
      color: white !important;
    }
    .link {
      color: white !important;
    }
    .post {
      display: flex;
      width: 100% !important;
      position: absolute;
      top: 40vh !important;
      left: 20px !important;
    }
    .contain1 {
      display: none;
    }
    .contain {
      text-align: left;
    }
    .searchTitle {
      padding-top: 5vh !important;
      border: none !important;
      text-align: left !important;
      position: absolute !important;
      top: 43vh !important;
      width: 100%!important;
      padding-left: 2vw;
    }
    .empty1 {
      height: 0px;
    }
    .footDiv {
      position: absolute;
      top: 98vh !important;
    }
    .empty1 {
      display: none;
    }
    .options {
      margin: 5px !important;
      display: flex;
      flex-wrap: wrap;
      max-width: 400px !important;
      margin-top: 20px !important;
      justify-content: flex-start !important;
    }
    .footDiv {
      position: absolute;
      top: 90vh;
      width: 30vw !important;
      margin: auto;
    }
    .resume {
      position: absolute;
      top: 150vh !important;
      width: 93vw;
    }
  }
  
  .deb {
    margin-right: 0.5rem;
    overflow-y: scroll;
    width: 24rem;
    border: 0.0625rem solid #949494 !important;
    border-radius: 0.5rem;
    z-index: 1;
    max-height: 300px;
  }
  
  .deb::-webkit-scrollbar {
    display: none;
  }
  
  .deb2 {
    margin-right: 0.5rem;
    width: 23rem;
    border: 0.0625rem solid #949494 !important;
    border-radius: 0.5rem;
    z-index: 1;
    max-height: 44px;
  }
  
  .btn {
    width: 110px;
    font-weight: 600;
    background-color: #2557a7;
    border: 0.0625rem solid transparent !important;
    max-height: 44px;
    border-radius: 8px;
  }
  
  .btn a {
    color: white;
  }
  
  .btn a:hover {
    color: white;
  }
  
  .search {
    display: flex;
    align-items: center;
    padding: 1.23rem 1rem;
    border-radius: 0.5rem;
    background-color: #fff;
    color: #2d2d2d;
    line-height: 1.125rem;
    overflow: hidden;
    height: 1.125rem;
  }
  
  .inpu {
    width: 100%;
    border: none;
    font-size: 1rem;
    line-height: 1.25rem;
    outline: none;
  }
  
  .inpu:focus {
    outline: none;
    border: none;
  }
  
  .forms {
    display: flex;
  }
  
  .lab {
    padding-right: 1rem;
    font-weight: 700;
    display: inline-block !important;
  }
  
  .inp {
    width: 100%;
    margin-right: 30px;
  }
  
  /* debouncing */
  
  .autocomplete {
    width: 100%;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0, 3);
  }
  
  .autocompleteItems {
    padding: 10px 5px;
    cursor: pointer;
    padding-left: 15px;
    color: black;
  }
  
  .buttn {
    background-color: #2557a7;
    border: 0.0625rem solid transparent !important;
    color: #fff !important;
    box-shadow: none !important;
    margin-left: 10px;
    border-radius: 8px;
    padding: 0.5rem 1.5rem;
    font-size: 0.875rem;
    letter-spacing: 0;
    font-weight: 700;
    line-height: 1.5rem;
  }
  
  .contain {
    text-align: center;
  }
  
  .contain1 {
    text-align: center;
  }
  
  .post {
    display: flex;
    padding: 0.5rem 0.5rem;
    margin: auto;
    position: absolute;
    top: 23vh;
    left: 35vw;
  }
  
  .post1 {
    display: flex;
    padding: 0.5rem 0.5rem;
    width: 30vw;
    margin: auto;
    position: absolute;
    top: 30vh !important;
    left: 35vw !important;
  }
  
  .highlight {
    font-size: 1rem !important;
    color: #2557a7 !important;
    letter-spacing: 0;
    font-weight: 700;
    line-height: 1.67;
    cursor: pointer;
    text-align: center;
  }
  
  .highlight:hover {
    text-decoration: underline #2557a7;
  }
  
  .para {
    font-weight: 400 !important;
    font-size: 1rem !important;
    line-height: 1.67;
  }
  
  .searchTitle {
    border-top: 2px solid #f6f6f6;
    padding-top: 48px;
    text-align: center;
    position: absolute;
    top: 40vh;
    width: 100vw;
    margin: auto;
  }
  
  .searchTitle h2 {
    margin: 0 auto;
    font-size: 25px;
  }
  
  .options {
    margin: 1rem auto 0;
    display: flex;
    flex-wrap: wrap;
    max-width: 800px;
    -webkit-box-pack: center;
    justify-content: center;
  }
  
  .empty1 {
    height: 100px;
  }
  
  .empty2 {
    height: 80px;
  }
  
  .footDiv {
    position: absolute;
    top: 82vh;
    width: 100vw;
  }
  
  .resume {
    position: absolute;
    top: 103vh;
    width: 100vw;
  }
  
  .head {
    display: flex;
    align-items: center;
  }
  
  .head2 {
    display: none;
  }
  
  @media (max-width: 620px) {
    .head {
      display: none;
    }
    .head2 {
      display: flex;
    }
  }
  
  .buttn:hover {
    background: rgb(22, 64, 129) !important;
  }
  
  .autocompleteItems:hover {
    background-color: lightblue!important;
  }