

 

h2{
    margin: 19px 0;
}

form{
    width: 100%;
}

input{
    display: block;
}
label{
    font-weight: bold;
}
input{
    width: 100%;
    padding:11px;
    border: 1px solid rgba(0,0,0,.34);
    border-radius:7px;
    margin: 8px 0 28px;
    font-size:17px;
    outline:none;
}


p{
    width: 90%;
    text-align:left;
}


button{
    width: 125px;
    background: rgb(22, 64, 129);
    padding:13px;
    font-weight: bold;
    font-size:15px;
    border-radius:5px;
    border: none;
    color: white;
    cursor: pointer;
}
p{
    text-align:left;
    margin-bottom: 40px;
}
 p{
    text-align:center;
}
p>span{
    color:rgb(22, 64, 129);
}
.upper-part{
    background-color: rgb(235, 242, 252);
}


